import { AudioMng, AudioObj } from "../index_AudioMng";

export class AudioLayer {

	/*== Constructer ============================================= */

	/*== Static ===================================================*/

	/*== Member ===================================================*/

	/** ID */
	public id : string = "";

	/** ID   */
	public audios : Array<string> = [];

	/**  */
	// 
	public priority : number = 5;

	/**  */
	public get volume () {
		return this._volume;
	}
	public set volume (val) {
		this.vol(val);
	}
	private _volume : number = -1;//-1: 
	
	/*== Event ====================================================*/

	/*== Cocos LifeCycle ==========================================*/

	// LIFE-CYCLE CALLBACKS:

	// onLoad () {}

	start () {
		
	}

	update (dt) {
		
		
	}

	
	/*== Public Function ==========================================*/

	/**  */
	public delete () : void {
		let toRm = [];
		for (let each of this.audios) {
			toRm.push(each);
		}
		for (let each of toRm) {
			this.remove(each);
		}
	}

	/**  */
	public get (id: string) : AudioObj {
		for (let each of this.audios) {
			if (each != id) continue;
			return AudioMng.audio(each);
		}
		return null;
	}

	/**  */
	public add (id: string) : AudioLayer {
		if (this.audios.indexOf(id) != -1) return this;
		
		let audioObj = AudioMng.audio(id);
		if (!audioObj) return this;

		this.audios.push(id);

		audioObj.addLayer(this.id);

		return this;
	}

	/**  */
	// AudioAudioLayer
	public move (id: string) : AudioLayer {
		let audioObj = AudioMng.audio(id);
		if (!audioObj) return this;

		for (let layerID of audioObj.layers) {
			let layer = AudioMng.layer(layerID);
			if (layer){
				layer.remove(id);
			}
		}

		return this.add(id);
	}
	
	/**  */
	public remove (id: string) : void {
		let idx = this.audios.indexOf(id);
		if (idx == -1) return;

		//    
		let audio = this.get(id);
		audio.removeLayer(this.id);

		//   
		this.audios.splice(idx, 1);
	}

	/**  */
	public vol (vol: number) : AudioLayer {
		this._volume = vol;
		this.eachAudio((audio)=>{
			audio.updateVolume();
		});
		return this;
	}

	/**  */
	public prio (priority: number) : AudioLayer {
		this.priority = priority;
		this.eachAudio((audio)=>{
			audio.updateLayers();
		});
		return this;
	}

	/**  */
	public play () : void {
		this.eachAudio((audio)=>{
			audio.pause();
		});
	}

	/**  */
	public pause () : void {
		this.eachAudio((audio)=>{
			audio.pause();
		});
	}

	/**  */
	public resume (id: string) : void {
		this.eachAudio((audio)=>{
			audio.resume();
		});
	}

	/**  */
	public stop (id: string) : void {
		this.eachAudio((audio)=>{
			audio.stop();
		});
	}

	/**  */
	public eachAudio (fn: (item: AudioObj)=>void) {
		for (let each of this.audios) {
			let audio = AudioMng.audio(each);
			if (!audio) continue;
			fn(audio);
		}
	}
	
	/*== Protected Function =======================================*/

	/*== Private Function =========================================*/


}

