			// 	"megaways":[2, 2, 6, 6, 3],
			// }

			// {
			// 	"stopPosList": [0, 24, 13, 28, 19],
			// 	"megaways":[6, 2, 2, 6, 3],
			// },
			// {
			// 	"stopPosList": [29, 35, 19, 30, 22],
			// 	"megaways":[6, 2, 3, 2, 3],
			// },
			// {
			// 	"stopPosList": [32, 32, 17, 0, 16],
			// 	"megaways":[3, 3, 6, 3, 2],
			// },

			//  / Tumbling*1
			// {
			// 	"stopPosList": [27, 13, 37, 24, 13],
			// 	"megaways":[6, 6, 3, 3, 2],
			// }

			// {
