// 
export class WinData {

    /**  () (line) */
	code : number = 0;
	
    /**  */
    symbol : number = 0;

    /**  */
    count : number = 0;

    /**  */
    winBonus : number = 0;
    
	/**  */
    path : Array<any> = [];
    
    /**  */
    public getCopy () : WinData {
        let newOne = new WinData();
        newOne.code = this.code;
        newOne.symbol = this.symbol;
        newOne.count = this.count;
        newOne.winBonus = this.winBonus;
        newOne.path = this.path.slice();
        return newOne;
    }
}